.repo {
  border: 2px solid #000;
  min-height: 360px;
  border-radius: 5px; }
  .repo article h3 {
    text-align: center; }
    .repo article h3 a {
      padding: 15px 0;
      display: block;
      color: #FFF;
      background: #ccc; }
      .repo article h3 a:hover {
        background: #000; }
  .repo article .header-repo {
    float: left;
    width: 100%; }
  .repo article .footer-repo {
    float: left;
    width: 100%;
    padding: 30px 20px 0; }
  .repo article .owner {
    text-align: center; }
  .repo article .avatar-owner {
    width: 60px !important;
    border-radius: 50%; }
